@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ma+Shan+Zheng&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-Content {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

footer {
  font-size: 15px;
  margin-bottom: 10px;
}
.Home {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.TitleCN {
    font-family: 'Microsoft YaHei', cursive;
    font-weight: normal;
    font-size: 10vh;
    margin: 0 0 50px 0;
}

h1 {
    font-family: 'Fredericka the Great', cursive;
    font-weight: lighter;
}

p {
    margin: 10px;
}

.Result {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 80vw;
    min-height: 100px;
    border: 3px solid white;
    border-radius: 10px;
}

img {
    width: 100px;
    display: none;
}



.Label {
    font-size: 50px;
    font-weight: bold;
}

textarea {
    resize: none;
    border-radius: 10px;
    height: 100px;
    width: 80vw;
    font-size: calc(10px + 2vmin);
    text-align: center;
    line-height: 100px;
    margin-bottom: 10px;
}
button {
    width: 200px;
    height: 50px;
    border-radius: 10px;
    font-size: 20px;
    margin: 20px;
    background-color: rgba(92, 209, 203, 0.726);
}
